@import url(https://fonts.googleapis.com/css?family=Blinker:200,400&display=swap);
body {
	margin: 0;
	font-family: 'Blinker';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

::-webkit-scrollbar {
	width: 10px;
}

::-webkit-scrollbar-thumb {
	background-color: #290b3d;
}
::-webkit-scrollbar-thumb:hover {
	background-color: #49156b;
}

::-webkit-scrollbar-track {
	background-color: #c4c4c4;
}

::-moz-selection {
	background: #3b085e;
	color: white;
}

::selection {
	background: #3b085e;
	color: white;
}

.Page {
	height: 100vh;
	width: 100%;
	position: fixed;
	-webkit-transition: opacity 100ms linear;
	transition: opacity 100ms linear;
}

.page-enter {
	opacity: 0;
}

.page-exit {
	opacity: 1;
}

.page-exit-active {
	opacity: 0;
}

.Route404 {
	height: 100vh;
	width: 100%;
	background-color: '#3f3383';
	background-image: url(/palette/static/media/bg.794a2815.svg);
	background-size: cover;
	display: flex;
	justify-content: center;
	align-content: center;
	flex-direction: column;
	text-align: center;
}

.Route404 h1, .Route404 h2{
	flex-direction: row;
	margin: 0;
	color: white;
}

.Route404 h1 {
	font-size: 150px;
	font-weight: 400;
}

.Route404 h2 {
	font-size: 50px;
	font-weight: 100;
}

.Route404 .Links {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-top: 50px;
}

.Route404 a {
	color: white;
	margin: 30px;
	text-decoration: none;
	font-size: 20px;
	letter-spacing: 1px;
}

.Route404 a:hover {
	color: #ddd;
}

